import React, { useContext, useMemo } from 'react'
import { ThemeContext } from 'styled-components'
import { Pair } from '@pancakeswap-libs/sdk'
import { Button, CardBody, Text } from '@pancakeswap-libs/uikit'
import { Link } from 'react-router-dom'
import CardNav from 'components/CardNav'
import Question from 'components/QuestionHelper'
import FullPositionCard from 'components/PositionCard'
import { useTokenBalancesWithLoadingIndicator } from 'state/wallet/hooks'
import { StyledInternalLink } from 'components/Shared'
import { LightCard } from 'components/Card'
import { RowBetween } from 'components/Row'
import { AutoColumn } from 'components/Column'

import { useActiveWeb3React } from 'hooks'
import { usePairs } from 'data/Reserves'
import { toV2LiquidityToken, useTrackedTokenPairs } from 'state/user/hooks'
import { Dots, Wrapper } from 'components/swap/styleds'
import useI18n from 'hooks/useI18n'
import PageHeader from 'components/PageHeader'
import AppBody from '../AppBody'

export default function Pool(props) {
  const theme = useContext(ThemeContext)
  const { account } = useActiveWeb3React()
  const TranslateString = useI18n()

  // fetch the user's balances of all tracked V2 LP tokens
  const trackedTokenPairs = useTrackedTokenPairs()
  const tokenPairsWithLiquidityTokens = useMemo(
    () => trackedTokenPairs.map((tokens) => ({ liquidityToken: toV2LiquidityToken(tokens), tokens })),
    [trackedTokenPairs]
  )
  const liquidityTokens = useMemo(() => tokenPairsWithLiquidityTokens.map((tpwlt) => tpwlt.liquidityToken), [
    tokenPairsWithLiquidityTokens,
  ])
  const [v2PairsBalances, fetchingV2PairBalances] = useTokenBalancesWithLoadingIndicator(
    account ?? undefined,
    liquidityTokens
  )

  // fetch the reserves for all V2 pools in which the user has a balance
  const liquidityTokensWithBalances = useMemo(
    () =>
      tokenPairsWithLiquidityTokens.filter(({ liquidityToken }) =>
        v2PairsBalances[liquidityToken.address]?.greaterThan('0')
      ),
    [tokenPairsWithLiquidityTokens, v2PairsBalances]
  )

  const v2Pairs = usePairs(liquidityTokensWithBalances.map(({ tokens }) => tokens))
  const v2IsLoading =
    fetchingV2PairBalances || v2Pairs?.length < liquidityTokensWithBalances.length || v2Pairs?.some((V2Pair) => !V2Pair)

  const allV2PairsWithLiquidity = v2Pairs.map(([, pair]) => pair).filter((v2Pair): v2Pair is Pair => Boolean(v2Pair))
  const viewHistory = () => {
    const historyBtn: HTMLElement = document.getElementsByClassName('sc-dlfnbm fxfdHI sc-hKgILt hElrcP')[3] as HTMLElement
    historyBtn.click()
  }
  const setting = () => {
    const settingBtn: HTMLElement = document.getElementsByClassName('sc-dlfnbm fxfdHI sc-hKgILt hElrcP')[4] as HTMLElement
    settingBtn.click()
  }

  return (
    <div className="content-wrapper content collapse show">
      {/* <CardNav activeIndex={1} /> */}
      <div className="swap-switch-wrapper">
          <div className="swap-switch">
              <div className="btn-placeholder btn btn-primary rounded-pill" data-float="right" />
              <button type='button' onClick={() => props.history.push('/swap')} className="btn btn-switch rounded-pill" data-target="swap" data-float="left">
                  swap
              </button>
              <button type='button' onClick={() => props.history.push('/pool')} className="btn btn-switch active rounded-pill" data-target="liquidity" data-float="right">
                  liquidity
              </button>
          </div>
      </div>
      <AppBody>
        <Wrapper className='swap content collapse show' id="pool-page">
          <PageHeader
            title={TranslateString(262, 'L i q u i d i t y')}
            description={TranslateString(1168, 'Add liquidity to receive LP tokens')}
          >
            {/* <Button id="join-pool-button" as={Link} to="/add/BNB">
              {TranslateString(168, 'Add Liquidity')}
            </Button>  */}
          </PageHeader>
          {/* <div className="liquidity-header">
            <h2 className="liquidity-title">Liquidity</h2>
            <div className="wrapper">
                <button type="button" onClick={()=>viewHistory()} className="btn btn-text-primary">
                    <svg
                        width="26"
                        height="25"
                        viewBox="0 0 26 25"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                    >
                        <path
                            d="M13.5 0C6.55588 0 0.90625 5.46738 0.90625 12.1875C0.90625 18.9076 6.55588 24.375 13.5 24.375C20.4441 24.375 26.0938 18.9076 26.0938 12.1875C26.0938 5.46738 20.4441 0 13.5 0ZM20.7656 10.6729C20.7656 10.8068 20.7106 10.9353 20.6128 11.03C20.5149 11.1247 20.3821 11.1779 20.2437 11.1779H16.6388C16.5355 11.1781 16.4345 11.1486 16.3486 11.0931C16.2627 11.0376 16.1958 10.9587 16.1563 10.8664C16.1168 10.7741 16.1065 10.6725 16.1267 10.5745C16.147 10.4765 16.1969 10.3865 16.27 10.316L17.6123 9.01699L17.2733 8.63555C16.6668 8.02624 15.9137 7.57186 15.0814 7.31305C14.2491 7.05423 13.3635 6.99904 12.5038 7.15241C11.6441 7.30578 10.837 7.66293 10.1548 8.19193C9.47258 8.72092 8.93643 9.40528 8.59431 10.1838C8.25218 10.9623 8.11475 11.8108 8.19428 12.6533C8.27381 13.4958 8.56784 14.3061 9.05007 15.0117C9.5323 15.7174 10.1877 16.2964 10.9577 16.697C11.7277 17.0976 12.5883 17.3073 13.4625 17.3074C14.5565 17.3064 15.6235 16.9779 16.5169 16.3668C17.4104 15.7557 18.0866 14.8921 18.4527 13.8943C18.4952 13.7781 18.5608 13.6712 18.6459 13.5795C18.7311 13.4879 18.834 13.4134 18.9489 13.3603C19.0638 13.3071 19.1884 13.2764 19.3155 13.2699C19.4427 13.2634 19.5699 13.2811 19.69 13.3222C19.8101 13.3632 19.9206 13.4268 20.0153 13.5091C20.11 13.5915 20.187 13.6912 20.2419 13.8023C20.2968 13.9135 20.3285 14.0341 20.3353 14.1572C20.342 14.2802 20.3237 14.4033 20.2812 14.5195C19.8397 15.7288 19.0631 16.7974 18.0365 17.6084C17.0098 18.4194 15.7725 18.9417 14.4598 19.1182C13.1471 19.2947 11.8095 19.1186 10.5932 18.6091C9.37696 18.0996 8.32883 17.2764 7.56338 16.2294C6.79792 15.1824 6.34461 13.9519 6.25298 12.6724C6.16135 11.3929 6.43493 10.1136 7.04381 8.97446C7.6527 7.83527 8.57346 6.87999 9.70547 6.21301C10.8375 5.54603 12.1372 5.19302 13.4625 5.19258C14.4406 5.18896 15.4091 5.37919 16.3084 5.75153C17.2076 6.12388 18.0186 6.67047 18.6913 7.35762L18.7252 7.39395L18.9862 7.68691L19.875 6.82676C19.9223 6.77989 19.9788 6.7426 20.0412 6.71705C20.1035 6.6915 20.1705 6.6782 20.2383 6.67793C20.3072 6.67739 20.3755 6.69007 20.4393 6.71526C20.5032 6.74044 20.5612 6.77762 20.6101 6.82465C20.6589 6.87167 20.6977 6.92761 20.7241 6.98922C20.7505 7.05083 20.764 7.1169 20.7638 7.18359L20.7656 10.6729Z"
                            fill="#940072"
                        />
                    </svg>
                </button>
                <button type="button" onClick={()=>setting()} className="btn btn-text-primary">
                    <svg
                        width="24"
                        height="16"
                        viewBox="0 0 24 16"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                    >
                        <path
                            d="M10.75 15.5H13.25C13.9375 15.5 14.5 14.9375 14.5 14.25C14.5 13.5625 13.9375 13 13.25 13H10.75C10.0625 13 9.5 13.5625 9.5 14.25C9.5 14.9375 10.0625 15.5 10.75 15.5ZM0.75 1.75C0.75 2.4375 1.3125 3 2 3H22C22.6875 3 23.25 2.4375 23.25 1.75C23.25 1.0625 22.6875 0.5 22 0.5H2C1.3125 0.5 0.75 1.0625 0.75 1.75ZM5.75 9.25H18.25C18.9375 9.25 19.5 8.6875 19.5 8C19.5 7.3125 18.9375 6.75 18.25 6.75H5.75C5.0625 6.75 4.5 7.3125 4.5 8C4.5 8.6875 5.0625 9.25 5.75 9.25Z"
                            fill="#940072"
                        />
                    </svg>
                </button>
            </div>
        </div> */}
        {/* <h5 className="liquidity-subtitle">Add liquidity to receive LP tokens</h5> */}

        <Button
          onClick={()=> props.history.push('/add/BNB')}
          style={{ width: '100%', fontFamily: "'Roboto',sans-serif" }}
          variant='primary'
          className="btn btn-panel btn-primary"
        >
          add liquidity
        </Button>
        {/* <button type="button" onClick={()=> props.history.push('/add/BNB')} className="btn btn-panel btn-primary">add liquidity</button> */}
        <div className="divider" style={{margin: '2rem 0'}} />
          <AutoColumn id='this' gap="lg" justify="center">
            <CardBody style={{width: '100%'}}>
              <AutoColumn gap="12px" style={{ width: '100%' }}>
                <RowBetween padding="0 8px">
                  <Text color={theme.colors.text}>{TranslateString(107, 'Your Liquidity')}</Text>
                  <Question
                    text={TranslateString(
                      1170,
                      'When you add liquidity, you are given pool tokens that represent your share. If you dont see a pool you joined in this list, try importing a pool below.'
                    )}
                  />
                </RowBetween>

                {!account ? (
                  <LightCard padding="40px">
                    <Text color="textDisabled" textAlign="center">
                      {TranslateString(156, 'Connect to a wallet to view your liquidity.')}
                    </Text>
                  </LightCard>
                ) : v2IsLoading ? (
                  <LightCard padding="40px">
                    <Text color="textDisabled" textAlign="center">
                      <Dots>Loading</Dots>
                    </Text>
                  </LightCard>
                ) : allV2PairsWithLiquidity?.length > 0 ? (
                  <>
                    {allV2PairsWithLiquidity.map((v2Pair) => (
                      <FullPositionCard key={v2Pair.liquidityToken.address} pair={v2Pair} />
                    ))}
                  </>
                ) : (
                  <LightCard padding="40px">
                    <Text color="textDisabled" textAlign="center">
                      {TranslateString(104, 'No liquidity found.')}
                    </Text>
                  </LightCard>
                )}

                <div>
                  <Text fontSize="14px" style={{ padding: '.5rem 0 .5rem 0' }}>
                    {TranslateString(106, "Don't see a pool you joined?")}{' '}
                    <StyledInternalLink id="import-pool-link" to="/find">
                      {TranslateString(108, 'Import it.')}
                    </StyledInternalLink>
                  </Text>
                  <Text fontSize="14px" style={{ padding: '.5rem 0 .5rem 0' }}>
                    {TranslateString(1172, 'Or, if you staked your LP tokens in a farm, unstake them to see them here.')}
                  </Text>
                </div>
              </AutoColumn>
            </CardBody>
          </AutoColumn>
        </Wrapper>
      </AppBody>
    </div>
  )
}
