import React, { useContext } from 'react'
import { useWeb3React } from '@web3-react/core'
import { useWalletModal} from '@pancakeswap-libs/uikit'
import useAuth from 'hooks/useAuth'
import useMetamask from 'hooks/useMetamask'

const Header: React.FC = (props) => {
  const { account } = useWeb3React()

  const { login, logout } = useAuth()
  const { onPresentConnectModal, onPresentAccountModal } = useWalletModal(login, logout, account ?? '')
  
  let address: string;

  if(account) {
    address = account.slice(0, 3).concat( "...").concat( account.slice(account.length - 3));
  }
  else address = "Connect";

  return (
    <nav className="navbar navbar-expand-lg navbar-dark">
      <a className="navbar-brand" href="/"> swap </a>
      <div className="wrapper">
        <button onClick={useMetamask} type="button" className="btn btn-primary rounded-pill">PriceAI</button>
        <button onClick={ account ? onPresentAccountModal : onPresentConnectModal} type="button" className="btn btn-primary rounded-pill">{address}</button>
      </div>
    </nav>
  )
}

export default Header
